// 
export default {
  // 
  mounted() {
    //  routerTab 
    this.$watch(
      'routeTab',
      function(val) {
        if (!val) return

        const tab = typeof val === 'string' ? { title: val } : val
        const { activeTab } = this.$routerTab || {}

        if (tab && activeTab) {
          for (const key in tab) {
            if (!['id', 'to'].includes(key)) {
              this.$set(activeTab, key, tab[key])
            }
          }
        }
      },
      {
        deep: true,
        immediate: true
      }
    )
  }
}
